import { Component, OnInit } from '@angular/core';
import { Model } from 'src/app/api/models/model';
import { ActivatedRoute, Router } from '@angular/router';
import { RequestService } from 'src/app/api/request.service';
import { DialogService } from 'src/app/api/util/dialog.service';
import { AuthUserService } from 'src/app/services/auth-user.service';
import { ModalController, ActionSheetController } from '@ionic/angular';
import { UtilArrayService } from 'src/app/services/util-array.service';

@Component({
  selector: 'app-months',
  templateUrl: './months.page.html',
  styleUrls: ['./months.page.scss'],
})
export class MonthsPage implements OnInit {


  public id;
  public balanceSheet;
  public modelBalanceSheet : Model;

  constructor(
    public route : ActivatedRoute,
    private router : Router,
    private requestService : RequestService,
    public dialogService : DialogService,
    public authUser : AuthUserService,
    private modalController : ModalController,
    public utilArray : UtilArrayService,
    public actionSheetController: ActionSheetController
  ) { 

    this.modelBalanceSheet = new Model('BalanceSheet',requestService);

    this.id = route.snapshot.paramMap.get('id');

  }

  ngOnInit() {
    this.init();
  }


  async init(){
    await this.dialogService.loadingProgres();

    this.modelBalanceSheet.api_functionModel(this.id, 'pageMonths').subscribe(
      response => {
        this.dialogService.dimissLoaging();

        if(response['status'] == 'success'){
          this.balanceSheet = response['data'];
        }
      },
      error => {
        this.dialogService.dimissLoaging();
      }
    )
  }
}
